      resizeNetworkContainer(ms, callback) {
        setTimeout(() => {
          // TODO move to config
          const offsetH = 150;
          const offsetW = 300;

          const winSize = this.getWindowSize();
          const svgSize = this.getSvgBoxSize();

          if (this.size.width > svgSize.w + offsetW || this.size.width < svgSize.w - offsetW / 3) {
            this.size.width = winSize.w < svgSize.w + offsetW ? svgSize.w + offsetW : winSize.w;
          }

          if (this.size.height > svgSize.h + offsetH || this.size.height < svgSize.h - offsetH / 3) {
            this.size.height = winSize.h < svgSize.h + offsetH ? svgSize.h + offsetH : winSize.h;
          }

          this.resizerBlocked = false;

          callback();
        }, ms);
      },
      getWindowSize() {
        return {
          h: window.innerHeight
              || document.documentElement.clientHeight
              || document.body.clientHeight,
          w: window.innerWidth
              || document.documentElement.clientWidth
              || document.body.clientWidth,
        }
      },
      getSvgBoxSize() {
        const rect = document.getElementById('l-nodes').getBBox();
        return {
          h: rect.height,
          w: rect.width,
        }
      },
      scrollToSvgBoxCenter() {
        const winSize = this.getWindowSize();

        const container = document.getElementById('network-container');

        const scrollTop = this.size.height > winSize.h ? (this.size.height - winSize.h) / 2 : 0;
        const scrollLeft = this.size.width > winSize.w ? (this.size.width - winSize.w) / 2 : 0;

        // scroll left doest work
        window.scrollTo({
          top: scrollTop,
          behavior: "smooth"
        });

        // scroll top doest work
        container.scrollTo({
          left: scrollLeft,
          behavior: "smooth"
        })
      },
      scrollToNode(node) {
        return () => {
          const winSize = this.getWindowSize();
          const container = document.getElementById('network-container');
          const scrollTop = node.y > winSize.h / 2 ? node.y - winSize.h / 2 : 0;
          const scrollLeft = node.x > winSize.w / 2 ? node.x - winSize.w / 2 : 0;

          // scroll left doest work
          window.scrollTo({
            top: scrollTop,
            behavior: "smooth"
          });

          // scroll top doest work
          container.scrollTo({
            left: scrollLeft,
            behavior: "smooth"
          });

          const circle = document.getElementById(`node-${node.id}`);
          circle.classList.add('pulse-ring');

          setTimeout(() => {
            circle.classList.remove('pulse-ring');
          }, 1500);
        };
      },
      //   linkCallback(link) {
      //     link._svgAttrs = {
      //       'marker-end': 'url(#m-end)'
      //     };
      //     return link
      //   },
      selectNode(node) {
        node = this.nodes.find(n => n.id === node.id);

        if (node) {
          this.scrollToNode(node)(); // It's decorator
        }
      },
      closeDialog(event) {
        this.nodeDialog = undefined
      },
      showDialog(event, node) {
        if (this.nodeDialog && this.nodeDialog.id === node.id) {
          this.closeDialog()
        } else {
          this.nodeDialog = Object.assign({}, node)
        }
      }
    },
    watch: {
      filteredNodes(val) {
        // hack for deep copy
        this.nodes = JSON.parse((JSON.stringify(val)));
      },
      filteredLinks(val) {
        // hack for deep copy
        this.links = JSON.parse((JSON.stringify(val)));
      }
    },
    computed: {
      filteredNodes() {
        let filteredNodes = [];

        if (this.selectedRoadmaps.length > 0) {
          filteredNodes = this.selectedNodes.filter(item => {
            return this.selectedRoadmaps.some(roadmap => roadmap.concepts.includes(item.id));
          });
        } else {
          filteredNodes = this.selectedNodes;
        }

        filteredNodes = this.allNodes.filter(node => {
          return filteredNodes.findIndex(n => n.id === node.id) !== -1;
        });

        let parents = [];
        filteredNodes.forEach(node => {
          node.parents.forEach(parentId => {
            if (parents.findIndex(n => n.id === parentId) === -1) {
              parents.push(this.allNodes.find(n => n.id === parentId));
            }
          });
        });

        if (this.selectedRoadmaps.length > 0) {
          filteredNodes = filteredNodes.concat(this.selectedRoadmaps);
        }

        filteredNodes = filteredNodes.concat(parents);

        return filteredNodes;
      },
      filteredLinks() {
        return this.allLinks.filter(link => {
          const sidExist = this.filteredNodes.findIndex(node => node.id === link.sid) !== -1;
          const tidExist = this.filteredNodes.findIndex(node => node.id === link.tid) !== -1;
          return sidExist && tidExist;
        })
      },
      options() {
        return {
          force: this.force,
          size: {
            w: this.size.width,
            h: this.size.height
          },
          nodeSize: this.nodeSize,
          nodeLabels: true,
          linkLabels: false,
          canvas: this.canvas
        }
      },
    },
    mounted() {
      const repoPath = 'https://api.github.com/repos/roadmap-project/dev-learning-guidelines/contents';
      const dataPath = '/database/content/vis.json';

      fetch(repoPath + dataPath)
          .then(res => res.json())
          .then(data => {
            data = atob(data.content);
            data = JSON.parse(data);

            this.allLinks = data['links'];
            this.allNodes = data['nodes'];
            this.treeview = data['treeview'];
            this.roadmaps = data['roadmaps'];

            // TODO ref node pre processing
            this.allNodes.forEach(node => {
              node._size = node.is_concept ? this.nodeSize : this.nodeSize * 1.3;
              node._color = node.is_concept ? this.color.concept : this.color.group;
            });

            this.roadmaps.forEach(roadmap => {
              roadmap._size = this.nodeSize * 1.5;
              roadmap._color = this.color.roadmap;
              roadmap.is_roadmap = true;
            });

          });

      this.resizeNetworkContainer(0, this.scrollToSvgBoxCenter);

      this.resizerEvent = setInterval(() => {
        if (!this.resizerBlocked) {
          this.resizeNetworkContainer(0, () => {
          });
        }
      }, 1000);
    },
    beforeDestroy() {
      clearInterval(this.resizerEvent);
    }
  }
</script>

