    padding: 5px;
    margin: 5px;
    text-align: center;
    border-radius: 4px;
  }

  .net {
    height: 100%;
    margin: 0
  }

  .node {
    stroke: #757575;
    stroke-opacity: 0.7;
    stroke-width: 2px;
    -webkit-transition: fill .5s ease;
    transition: fill .5s ease;
    fill: #dcfaf3
  }

  .node.selected {
    stroke: #caa455;
  }

  .link {
    stroke: #757575;
    stroke-opacity: 0.45;
  }

  .link, .node {
    stroke-linecap: round;
  }

  .node:hover {
    stroke: #757575;
    stroke-width: 3px;
  }

  .node.pinned {
    stroke: rgba(190, 56, 93, .7)
  }

  .node:hover {
    transform: translateY(-0.1em);
  }

  .link.selected {
    stroke: rgba(202, 164, 85, .6)
  }

  .curve {
    fill: none
  }

  .link-label, .node-label {
    fill: #424242;
    font-family: Verdana, serif;
    font-size: x-small;
    font-weight: bolder;
  }

  .link-label {
    -webkit-transform: translateY(-.5em);
    transform: translateY(-.5em);
    text-anchor: middle
  }

  .pulse-ring {
    animation: pulsate infinite 0.5s;
  }

  @-webkit-keyframes pulsate {
    0% {
      stroke: rgba(190, 56, 93, .6);
      stroke-width: 2px;
    }
    50% {
      stroke: rgba(190, 56, 93, 1);
      stroke-width: 10px;
    }
    100% {
      stroke: rgba(190, 56, 93, .6);
      stroke-width: 2px;
    }
  }
</style>
